"use client";

import { useEffect, useState } from "react";
import Link from "next/link";
import { api } from "@/lib/api";
import { Partido, PosicionTabla } from "@/types";
import { format } from "date-fns";
import { es } from "date-fns/locale";
import { toast } from "sonner";

// UI Components
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Trophy, CalendarDays, PlusCircle, Save, ChevronLeft, ChevronRight } from "lucide-react";
import { cn } from "@/lib/utils";

export default function TorneoDashboard() {
    const [activeTab, setActiveTab] = useState("fixture");
    const [serieSeleccionada, setSerieSeleccionada] = useState<string>("1era");

    const [partidos, setPartidos] = useState<Partido[]>([]);
    const [tabla, setTabla] = useState<PosicionTabla[]>([]);
    const [loading, setLoading] = useState(true);

    // ESTADO PARA NAVEGACIÓN DE FECHAS
    const [fechaActual, setFechaActual] = useState<number>(1);

    // Cargar datos
    const fetchData = async () => {
        setLoading(true);
        try {
            const [partidosData, tablaData] = await Promise.all([
                api.getPartidos(),
                api.getTablaPosiciones(serieSeleccionada)
            ]);
            setPartidos(partidosData);
            setTabla(tablaData);

            // Intentar detectar la fecha más reciente con partidos (opcional) o quedarse en 1
            // Por defecto iniciamos en la 1
        } catch (error) {
            console.error(error);
            toast.error("Error al cargar datos");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        fetchData();
    }, [serieSeleccionada]);

    // Guardar Goles
    const handleUpdateScore = async (id: number) => {
        const inputLocal = document.getElementById(`local-${id}`) as HTMLInputElement;
        const inputVisita = document.getElementById(`visita-${id}`) as HTMLInputElement;

        if (!inputLocal || !inputVisita) return;

        try {
            await api.updateResultado(id, Number(inputLocal.value), Number(inputVisita.value));
            toast.success("Resultado actualizado");
            fetchData();
        } catch (error) {
            toast.error("Error al guardar resultado");
        }
    };

    // Filtrar partidos por serie
    const partidosDeLaSerie = partidos.filter(p => p.serie === serieSeleccionada);

    // Obtener lista de números de fecha únicos
    const fechasDisponibles = Array.from(new Set(partidosDeLaSerie.map(p => p.fecha_numero))).sort((a, b) => a - b);

    // Filtrar partidos de la FECHA SELECCIONADA
    const partidosVisualizados = partidosDeLaSerie.filter(p => p.fecha_numero === fechaActual);

    return (
        <div className="container mx-auto py-8 max-w-6xl space-y-8">

            {/* HEADER */}
            <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4">
                <div>
                    <h1 className="text-3xl font-bold text-gray-900">Panel del Torneo</h1>
                    <p className="text-gray-500">Gestiona partidos y visualiza estadísticas.</p>
                </div>
                <div className="flex items-center gap-3">
                    <Link href="/torneo/generar">
                        <Button variant="outline">
                            <PlusCircle className="mr-2 h-4 w-4" /> Nuevo Torneo
                        </Button>
                    </Link>
                    <Select value={serieSeleccionada} onValueChange={setSerieSeleccionada}>
                        <SelectTrigger className="w-[180px] bg-white">
                            <SelectValue placeholder="Seleccionar Serie" />
                        </SelectTrigger>
                        <SelectContent>
                            <SelectItem value="1era">Primera Serie</SelectItem>
                            <SelectItem value="2da">Segunda Serie</SelectItem>
                            <SelectItem value="3era">Tercera Serie</SelectItem>
                        </SelectContent>
                    </Select>
                </div>
            </div>

            <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
                <TabsList className="grid w-full max-w-md grid-cols-2 mb-6">
                    <TabsTrigger value="fixture"><CalendarDays className="mr-2 h-4 w-4" /> Fixture</TabsTrigger>
                    <TabsTrigger value="tabla"><Trophy className="mr-2 h-4 w-4" /> Tabla de Posiciones</TabsTrigger>
                </TabsList>

                {/* --- TAB: FIXTURE MEJORADO --- */}
                <TabsContent value="fixture" className="space-y-6 animate-in fade-in slide-in-from-bottom-4 duration-500">

                    {fechasDisponibles.length > 0 ? (
                        <>
                            {/* BARRA DE NAVEGACIÓN DE FECHAS */}
                            <div className="flex items-center justify-center gap-2 bg-white p-2 rounded-lg shadow-sm border overflow-x-auto">
                                <Button
                                    variant="ghost"
                                    size="icon"
                                    onClick={() => setFechaActual(prev => Math.max(prev - 1, fechasDisponibles[0]))}
                                    disabled={fechaActual === fechasDisponibles[0]}
                                >
                                    <ChevronLeft className="h-4 w-4" />
                                </Button>

                                <div className="flex gap-2 overflow-x-auto no-scrollbar px-2 max-w-[300px] md:max-w-md snap-x">
                                    {fechasDisponibles.map((num) => (
                                        <Button
                                            key={num}
                                            variant={fechaActual === num ? "default" : "ghost"}
                                            className={cn(
                                                "snap-center min-w-[80px] flex-col h-12 gap-0",
                                                fechaActual === num ? "bg-blue-600 hover:bg-blue-700 text-white" : "text-gray-500"
                                            )}
                                            onClick={() => setFechaActual(num)}
                                        >
                                            <span className="text-[10px] font-normal uppercase">Fecha</span>
                                            <span className="text-lg font-bold leading-none">{num}</span>
                                        </Button>
                                    ))}
                                </div>

                                <Button
                                    variant="ghost"
                                    size="icon"
                                    onClick={() => setFechaActual(prev => Math.min(prev + 1, fechasDisponibles[fechasDisponibles.length - 1]))}
                                    disabled={fechaActual === fechasDisponibles[fechasDisponibles.length - 1]}
                                >
                                    <ChevronRight className="h-4 w-4" />
                                </Button>
                            </div>

                            {/* LISTADO DE PARTIDOS */}
                            <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-4">
                                {partidosVisualizados.map((partido) => (
                                    <Card key={partido.id} className="overflow-hidden hover:shadow-md transition-shadow">
                                        <div className={`h-1 w-full ${partido.estado === 'finalizado' ? 'bg-green-500' : 'bg-gray-200'}`} />
                                        <CardContent className="p-5">
                                            <div className="text-xs text-center text-gray-400 mb-4 font-mono flex justify-center items-center gap-2">
                                                <CalendarDays className="h-3 w-3" />
                                                {format(new Date(partido.dia_hora), "dd/MM/yyyy HH:mm", { locale: es })}
                                            </div>

                                            <div className="flex justify-between items-center gap-2 mb-4">
                                                {/* Local */}
                                                <div className="flex flex-col items-center w-1/3">
                                                    <span className="font-bold text-sm text-center truncate w-full">{partido.local?.nombre || "Local"}</span>
                                                </div>

                                                {/* Inputs Goles */}
                                                <div className="flex items-center gap-2">
                                                    <Input
                                                        id={`local-${partido.id}`}
                                                        type="number"
                                                        className="w-12 h-10 text-center text-lg font-bold p-0"
                                                        defaultValue={partido.goles_local}
                                                    />
                                                    <span className="font-bold text-gray-300">-</span>
                                                    <Input
                                                        id={`visita-${partido.id}`}
                                                        type="number"
                                                        className="w-12 h-10 text-center text-lg font-bold p-0"
                                                        defaultValue={partido.goles_visita}
                                                    />
                                                </div>

                                                {/* Visita */}
                                                <div className="flex flex-col items-center w-1/3">
                                                    <span className="font-bold text-sm text-center truncate w-full">{partido.visita?.nombre || "Visita"}</span>
                                                </div>
                                            </div>

                                            <Button
                                                size="sm"
                                                variant="secondary"
                                                className="w-full text-xs h-8"
                                                onClick={() => handleUpdateScore(partido.id)}
                                            >
                                                <Save className="w-3 h-3 mr-1" /> Actualizar
                                            </Button>
                                        </CardContent>
                                    </Card>
                                ))}
                                {partidosVisualizados.length === 0 && (
                                    <div className="col-span-full text-center py-10 text-gray-400 bg-gray-50 rounded-lg border border-dashed">
                                        No hay partidos programados para esta fecha en esta serie.
                                    </div>
                                )}
                            </div>
                        </>
                    ) : (
                        <div className="text-center py-20 bg-gray-50 rounded-lg border border-dashed">
                            <p className="text-gray-500">No hay partidos programados para esta serie.</p>
                        </div>
                    )}
                </TabsContent>

                {/* --- TAB: TABLA (Sin cambios mayores) --- */}
                <TabsContent value="tabla" className="animate-in fade-in slide-in-from-bottom-4 duration-500">
                    <Card>
                        <CardHeader>
                            <CardTitle>Tabla General - {serieSeleccionada}</CardTitle>
                        </CardHeader>
                        <CardContent>
                            <div className="overflow-x-auto">
                                <table className="w-full text-sm text-left">
                                    <thead className="bg-gray-100 uppercase text-xs text-gray-600">
                                        <tr>
                                            <th className="px-4 py-3 text-center">#</th>
                                            <th className="px-4 py-3">Club</th>
                                            <th className="px-4 py-3 text-center font-bold">PTS</th>
                                            <th className="px-4 py-3 text-center">PJ</th>
                                            <th className="px-4 py-3 text-center hidden md:table-cell">PG</th>
                                            <th className="px-4 py-3 text-center hidden md:table-cell">PE</th>
                                            <th className="px-4 py-3 text-center hidden md:table-cell">PP</th>
                                            <th className="px-4 py-3 text-center hidden md:table-cell">GF</th>
                                            <th className="px-4 py-3 text-center hidden md:table-cell">GC</th>
                                            <th className="px-4 py-3 text-center">DIF</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {tabla.map((fila, index) => (
                                            <tr key={index} className="border-b hover:bg-gray-50 transition-colors">
                                                <td className="px-4 py-3 text-center font-medium text-gray-500">{index + 1}</td>
                                                <td className="px-4 py-3 font-semibold text-gray-800 flex items-center gap-2">
                                                    {/* Mostrar logo si existe */}
                                                    {fila.logo ? (
                                                        <img src={fila.logo} alt={fila.club} className="w-6 h-6 object-contain" />
                                                    ) : (
                                                        <div className="w-6 h-6 bg-gray-200 rounded-full flex items-center justify-center text-[10px] text-gray-500">
                                                            {fila.club.substring(0, 2).toUpperCase()}
                                                        </div>
                                                    )}
                                                    {fila.club}
                                                </td>
                                                <td className="px-4 py-3 text-center font-bold text-blue-600 bg-blue-50">{fila.pts}</td>
                                                <td className="px-4 py-3 text-center">{fila.pj}</td>
                                                <td className="px-4 py-3 text-center hidden md:table-cell text-gray-500">{fila.pg}</td>
                                                <td className="px-4 py-3 text-center hidden md:table-cell text-gray-500">{fila.pe}</td>
                                                <td className="px-4 py-3 text-center hidden md:table-cell text-gray-500">{fila.pp}</td>
                                                <td className="px-4 py-3 text-center hidden md:table-cell text-gray-500">{fila.gf}</td>
                                                <td className="px-4 py-3 text-center hidden md:table-cell text-gray-500">{fila.gc}</td>
                                                <td className="px-4 py-3 text-center font-medium">{fila.dif > 0 ? `+${fila.dif}` : fila.dif}</td>
                                            </tr>
                                        ))}
                                    </tbody>
                                </table>
                            </div>
                        </CardContent>
                    </Card>
                </TabsContent>
            </Tabs>
        </div>
    );
}